@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  color: #fdfdfd;
}

.Home-Page {
  height: 100vh;
  width: 100%;
}

.Header {
  background: #202024;
  display: flex;
  justify-content: space-around;
  padding: 1.5em;
  width: 100%;
}

.Nav-Bar {
  width: 39%;
}

.Nav-Bar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.Nav-Bar ul a {
  font-weight: 500;
  color: #fdfdfd;
  text-decoration: none;
  transition: all 0.1s ease-out;
}

.Nav-Bar ul a:hover {
  color: #5d2de2;
}

.App {
  background: #121214;
  width: 100%;
}

#About-Me {
  background: #202024;
  height: 88vh;
  width: 100%;
}

#Projects {
  height: 100vh;
  width: 100%;
}

#Services {
  background: #202024;
  height: 88vh;
  width: 100%;
}

#Skills {
  height: 88vh;
  width: 100%;
}

.Footer {
  background: #5429cf;
  height: 30vh;
  width: 100%;
}

